@import url('https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700|Open+Sans:400');

body {
  line-height: 1.2;
  font-family: $secondary-font;
  -webkit-font-smoothing: antialiased;
}

p {
  font-family: $secondary-font;
  color: $text-color;
  font-size:16px;
  line-height: 1.5;
}

h1,h2,h3,h4,h5,h6 {
  font-family: $primary-font;
}

h1, .h1{
  font-size: 50px;
}

h2, .h2{
  font-size: 35px;
}

h3, .h3{
  font-size: 30px;
}

h4, .h4{
  font-size: 25px;
}

h5, .h5{
  font-size: 20px;
}

h6, .h6{
  font-size: 16px;
}