// 1.2. Button Styles
.btn{
    font-size: 12px;
    border-radius: 0;
    text-transform: uppercase;
    padding: 10px 20px;
    border: none;
    border-radius: 2px;
    font-family: $primary-font;
    font-weight: 500;
    font-size: 20px;
}

.btn-primary{
    background: $primary-color;
    color: $white;
    position: relative;
    z-index: 1;
    &:active{
        background: $primary-color !important;
    }
    &::before{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background: #b58e58;
        top: 0;
        left: 0;
        z-index: -1;
        transition: transform .5s ease;
        transform: scaleX(0);
        transform-origin: right;
    }
    &:hover{
        background: $primary-color;
        &::before{
            transform: scaleX(1);
            transform-origin: left;
        }
    }
}

.btn-sm{
    padding: 15px 30px;
}

.btn-dark{
    background-color: #313131;
}