// 2.1. Main Navgation
.main-nav{
    padding: 20px 30px;
    background: $white;
    box-shadow: 0 0 20px 5px rgba($color: $black, $alpha: .09);
    .navbar-brand{
        font-family: $primary-font;
        font-weight: 600;
        color: $text-color-dark;
    }
    .navbar-nav{
        .nav-item{
            .nav-link{
                color: $text-color;
                padding-left: 20px;
                padding-right: 20px;
                font-size: 14px;
                position: relative;
                z-index: 1;
                i{
                    margin-right: 5px;
                }
            }
            &:last-child{
                .nav-link{
                    padding-right: 0;
                }
            }
        }
    }
}
