.footer{
    padding: 40px 0 10px;
    background: $black;
    &-links{
        li{
            a{
                display: block;
                padding: 0 20px;
                font-size: 20px;
                text-transform: uppercase;
                color: #7B7B7B;
                text-decoration: none;
            }
        }
    }
}

.subscription-form{
    p{
        font-size: 18px;
        margin: 20px 0;
    }
    margin-bottom: 100px;
}

.social-list{
    li{
        a{
            display: block;
            padding: 0 25px;
            text-decoration: none;
            i{
                font-size: 22px;
                color: #7B7B7B;
            }
        }
    }
}