body{
    // background-image: url("../images/body-texture.png");
    // background-repeat: repeat;
    background-color: $white; 
}

.section{
    padding: 130px 0;
}

.form-control{
    height: 55px;
    padding: 15px;
    font-size: 14px;
    border-radius: 3px;
    margin-bottom: 20px;
    background: transparent;
    &:focus{
        outline: 0;
        box-shadow: none;
        border-color: $primary-color;
        background: transparent;
    }
}

textarea.form-control{
    height: auto;
}

.custom-control-label{
    font-size: 14px;
    letter-spacing: 1px;
    cursor: pointer;
    &::before, &::after{
        top: 0;
    }
}

.input-group{
    .form-control{
        border-right: 0;
    }
    &-text{
        background: transparent;
        border-left: 0;
    }
}

.item-category{
    ul{
        .list-inline-item{
            &:not(:last-child){
                margin-right: 3px;
            }
            font-family: $primary-font;
            i{
                font-size: 10px;
                margin-right: 7px;
            }
        }
    }
}

.item-table{
    padding: 20px 25px;
    background: $white;
    border-radius: 6px;
    box-shadow: 0 3px 5px 3px rgba($color: $black, $alpha: .08); 
    .table{
        margin-top: 10px;
        text-align: center;
        font-family: $primary-font;
        td, th{
            padding: 15px 5px;
            vertical-align: middle;
        }
        input{
            text-align: center;
            width: 60px;
            border-width: 1px;
            border-style: solid;
            border-color: $primary-color;
            border-radius: 2px;
            padding: 10px 5px;
        }
        button{
            border: none;
            background: #60CAC6;
            padding: 10px 20px;
            box-shadow: 0 3px 5px 3px rgba($color: $black, $alpha: .08);
            border-radius: 3px;
            color: $white; 
            cursor: pointer;
        }
    }
}